.sidebar {
    flex: 0.3;
    color: white;
    background-color: var(--slack-color);
    border-top: 1px solid #49274b;
    max-width: 260px;
    overflow: scroll;
}
.sidebar::-webkit-scrollbar{
    display: none;
}

.sidebar__header {
    display: flex;
    border-bottom: 1px solid #49274b;
    padding-bottom: 10px;
    padding: 13px;
}

.sidebar__header>.MuiSvgIcon-root {
    padding: 8px;
    color: #49274b;
    font-size: 18px;
    background-color: white;
    border-radius: 999px;
}

.sidebar__info {
    flex: 1;
}

.sidebar__info>h2 {
    font-size: 15px;
    font-weight: 900;
    margin-bottom: 5px;
}

.sidebar__info>h3 {
    display: flex;
    align-items: center;
    font-size: 13px;
    font-weight: 400;
}

.sidebar__info>h3>.MuiSvgIcon-root {
    color: green;
    font-size: 14px;
    margin-top: 1px;
    margin-right: 2px;
}
@media screen and (max-width:550px){
    .sidebar{
        flex:0.4 !important;
    }
}
@media screen and (max-width:380px){
    .sidebar{
        flex:0.5 !important;
        height:130vh;
    }
}