.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
    background-color: var(--slack-color);
    color: white;
    /* width: 200% !important; */
}

.header__left {
    flex: 0.3;
    display: flex;
    align-items: center;
    margin-left: 20px;

}

.header__left .MuiSvgIcon-root {
    margin-left: auto;
    margin-right: 30px;
}

.header__search {
    flex: 0.4;
    background-color: #421f44;
    text-align: center;
    display: flex;
    padding: 0 60px;
    color: gray;
    border: 1px gray solid;
    border-radius: 999px;
}

.header__search>input {
    background-color: transparent;
    border: none;
    text-align: center;
    min-width: 35vw;
    outline: none;
}

.header__right {
    flex: 0.3;
    display: flex;
    align-items: flex-end;
}

.header__right>.MuiSvgIcon-root {
    margin-left: auto;
    margin-right: 20px;
}
/* @media screen and (max-width:1100px){
    .header{
        width:250% !important;
    }
} */
@media screen and (max-width:550px){
    .header__left .MuiSvgIcon-root{
        display: none;
    }
    .header{
        width: 140% !important ;
    }
}
@media screen and (max-width:900px){
    .header{
        width: 140% !important;
    }

}
@media screen and (max-width:1400px){
        .header{
            width: 140% !important;
        }
}
@media screen and (max-width:1100px){
           .header{
               width: 140% !important;
           }
}
@media screen and (max-width:400px){
    .header{
        width: 140%;
        padding-top: 15px;
        padding-bottom: 15px;
    }
}