.chatInput {
    border-radius: 20px;
    /* margin-right: 50px; */

    /* margin-right: 500px; */
}

.chatInput>form{
    position: relative;
    display: flex;
    justify-content: center;
}

.chatInput>form>input{
    position: fixed;
    /* margin-right: 150px; */
    bottom: 30px;
    width: 100%;
    outline: none;
    border: 1px solid gray;
    border-radius: 10px;
    padding: 10px;
    padding-right: 0px !important;
}
.chatInput>form>button{
    display: none !important;
}

@media screen and (max-width:1400px){
    .chatInput>form>input{
        width: 68% !important;
    }

}
@media screen and (max-width:550px){
    .chatInput>form>input{
        width: 63% !important;
        /* padding-top: 20px; */
        
        padding-right: 30px;
    }
   
}
@media screen and (max-width:380px){
    .chatInput>form>input{
        width: 55% !important;
        margin-left: 0px;
        padding-left: 24px;
    }
}